import React, { useState } from 'react';
import './modal.css';


const SwipeGallery = (props) => {

  const [longTouch, setLongTouch] = useState(false);
  const imageItems = props.imageItems;
  const screenWidth = props.screenWidth;
  const maxNumber = props.maxNumber + 1;
  const [touchState, setTouchState] = useState({
    startX: 0,
    followX : 0,
    absMoveX: 0,
    imageIndex: props.imageIndex,
    bandWidth: {width: 100*maxNumber + '%'},
    swipeBand: {transform: 'translate3d(-' + props.imageIndex* screenWidth + 'px, 0, 0)'},
    swipeImage: {},
    bandClass: '',
    imageClass: ''
  });

  const touchStart = (e) => {
    setLongTouch(false);

    setTimeout(() => setLongTouch(true), 300);

    const moveX = e.touches[0].pageX; 
    setTouchState((prev) => ({
      ...prev,
      startX: moveX,
      bandClass: '',
      imageClass: '',
    }));
  };

  const touchMove = (e) => {
    const moveX = e.touches[0].pageX;

    setTouchState((prev) => {
      const followX = prev.imageIndex * screenWidth + (prev.startX - moveX);
      // const panX = 100 - followX / 6;
      const maxSwipe = screenWidth*(maxNumber - 1);
      const swipeBand = followX < maxSwipe
        ? {transform: 'translate3d(-' + followX + 'px, 0, 0)'}
        : prev.swipeBand;

      // const swipeImage = panX < 100 
      //   ? {transform: 'translate3d(-' + panX + 'px, 0, 0)'}
      //   : prev.swipeImage;

      return {
        ...prev,
        followX : followX,
        swipeBand : swipeBand,
        // swipeImage : swipeImage
      }
    });
  };

  const touchEnd = () => {

    setTouchState((prev) => {
      const absMoveX = Math.abs(prev.imageIndex * screenWidth - prev.followX);
      const forImageIndex = 
        (absMoveX > screenWidth/2 || longTouch === false) 
            ? (prev.followX > prev.imageIndex * screenWidth && prev.imageIndex < maxNumber-1)
              ? prev.imageIndex + 1 
              : (prev.followX < prev.imageIndex*screenWidth && prev.imageIndex > 0)
                ? prev.imageIndex - 1 : prev.imageIndex
            : prev.imageIndex;

      return {
        ...prev,
        absMoveX: absMoveX,
        imageIndex: forImageIndex
      }
    });

    setTouchState((prev) => {
      // const number = 100-prev.imageIndex*50;
      return {
      ...prev,
      bandClass: 'animate',
      swipeBand: {transform: 'translate3d(-' + prev.imageIndex*screenWidth + 'px, 0, 0)'},
      // imageClass: 'animate',
      // swipeImage: {transform: 'translate3d(-' + number+ 'px, 0, 0)'}
    }});
  };

  return (
    <>
    <div 
      className="image_box swipable"
    >
    <h3 className="image_index">{touchState.imageIndex + 1} / {maxNumber}</h3>
      <div 
        className="swipe_container"
        style = {{width: screenWidth}}
      >
        <div className = "swipe_inner_container">
          <ul 
            className={"image_band " + touchState.bandClass}
            style={{...touchState.swipeBand, ...touchState.bandWidth}}
            onTouchStart = {touchStart}
            onTouchMove = {touchMove}
            onTouchEnd = {touchEnd}
          >
            {imageItems.slice(0, maxNumber).map((e, i) => 
              <li 
                key={i}
                className="slide-wrapper"
                style = {{width: screenWidth}}
              >
                <div 
                  className={"image_container " + touchState.imageClass}
                  style = {touchState.swipeImage}
                >
                  <img 
                  src={e.image}
                  alt="images in modal"
                  />
                </div>
              </li>
            )}
          </ul>
        </div>
      </div>
    </div>
    </>
  )
};

export default SwipeGallery;
