import React, { useEffect, useState } from 'react';
import './modal.css';
import { Link } from "gatsby";
import SwipeGallery from './swipeGallery';

const Modal = (props) => {
  const [imageIndex, setImageIndex] = useState(props.startIndex);
  const [currentImage, setCurrentImage] = useState("");
  const [categoryName, setCategoryName] = useState("");

  const imageItems = props.imageItems;
  const maxNumber = props.maxNumber;

  const setImageItem = (index) => {
    setCurrentImage(imageItems[index].image);
    setCategoryName(imageItems[index].category);
  }

  useEffect(() => {
    setImageItem(imageIndex);
  }, [imageIndex])

  const advance = (step) => {
    setImageIndex(current => {
      const next = current + step;
      if(next > maxNumber) {
        return 0;
      } else if(next < 0) {
        return maxNumber;
      }
      return next;
    });
  };


  return (
    <div className="modal_container">
      <div className="white_box">
        <div className="left_box">
          <button 
            className="left_modal_button"
            onClick={() => advance(-1)}
          >
          <i className="fas fa-chevron-left fontFour"></i>
          </button>
        </div>
        <div className="close_box">
          <button 
            className="close_modal_button"
            onClick={props.onClose}
          >&times;</button>
        </div>
        <div className="title_box">
          <span>
            <button className="link_category_button">
              <Link to={"/projects/#" + categoryName}>
                <i className="far fa-folder"></i>{categoryName} /
              </Link>
            </button>
            <h2 className="modal_title modal_font">
              Project Title
            </h2>
          </span>
        </div>

        <UseViewport 
          currentImage= {currentImage}
          imageItems = {imageItems}
          imageIndex = {imageIndex}
          maxNumber = {maxNumber}
        />

        <div className="comment_box">
          <h3 className="modal_comment modal_font">
            I am the Goran san ! Bla bla bla bla bla~
          </h3>
        </div>
        <div className="right_box">
          <button 
            className="right_modal_button"
            onClick={() => advance(1)}
          >
            <i className="fas fa-chevron-right fontFour"></i>
          </button>
        </div>
        <div className="empty_box"></div>
      </div>

    </div>
  )
};

const UseViewport = (props) => {
  const [width, setWidth] = useState(window.innerWidth);
  const breakpoint = 992;
  // console.log(width);

  useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  return width < breakpoint 
    ? <SwipeGallery 
        imageItems = {props.imageItems}
        imageIndex = {props.imageIndex}
        maxNumber = {props.maxNumber}
        screenWidth = {width}
      /> 
    : <NonSwipeGallery 
        currentImage = {props.currentImage}
        imageIndex = {props.imageIndex}
      />
};

const NonSwipeGallery = (props) => {
  
  return (
    <div className="image_box">
    <img 
      src={props.currentImage}
      className= "modal_image"
      alt="images in modal"
    ></img>
  </div>
  )
};

export default Modal;